@use 'src/styles/vars';

.avh {
    width: 100vw;

    .avh__presentation {
        margin: 1rem auto;

        p {
            margin: 1rem;
            padding-left: 1rem;
            padding-right: 1rem;
        }

        ul {
            margin: 1rem auto;
            padding-left: 4rem;
            padding-right: 1rem;

            li {
                margin-top: 1rem;
                display: flex;
                align-items: center;
            }
        }

        img {
            width: 100%;
            height: auto;
        }
    }

    .avh__stat {
        display: flex;
        justify-content: space-evenly;
        height: auto;
        min-height: 210px;

        .avh__stat__item {
            width: auto;
            min-width: 200px;
            height: auto;
            min-height: 210px;
            margin: auto;
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            box-sizing: border-box;

            .stat__item-number {
                border: 1px vars.$violet solid;
                border-radius: 50%;
                height: 120px;
                width: 120px;
                display: flex;
                justify-content: center;
                align-items: center;
                font-size: 2rem;
                font-weight: bold;

                &:hover {
                    height: 150px;
                    width: 150px;
                    font-size: 2.5rem;
                    border: vars.$violet solid 2px;
                    box-shadow: 0px -1px 22px 0px vars.$violet;
                    -webkit-box-shadow: 0px -1px 22px 0px vars.$violet;
                    -moz-box-shadow: 0px -1px 22px 0px vars.$violet;
                }
            }

            .stat__item-category {
                margin: 1rem;
                font-size: 1.3rem;
                font-weight: bold;
            }
        }
    }

    .avh__button {
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        font-weight: bold;
        font-size: 1.5rem;
        margin: 1rem;

        p {
            margin: 1rem;
        }

        .avh__button-link {
            width: 20vw;
            min-width: 10rem;
            height: 3rem;
            border-radius: 10px;
            border: 2px solid transparent;
            background-color: vars.$violet;
            color: vars.$lightGrey;
            font-weight: bold;
            font-size: 1.2rem;
            display: flex;
            justify-content: center;
            align-items: center;

            &:hover {
                color: vars.$violet;
                background-color: transparent;
                border: vars.$violet solid 2px;
                box-shadow: 0px -1px 22px 0px vars.$violet !important;
                -webkit-box-shadow: 0px -1px 22px 0px vars.$violet !important;
                -moz-box-shadow: 0px -1px 22px 0px vars.$violet !important;
            }
        }
    }
}

@media screen and (max-width: 700px) {
    .avh {

        .avh__stat {
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
        }
    }
}


@media screen and (max-width: 500px) {
    .avh {
        padding-right: 2rem !important;
    }
}